___CSS_LOADER_EXPORT___.push([module.id, `	* {
		padding: 0;
		margin: 0;
		box-sizing: border-box;
		font-family: 'Roboto', sans-serif;
	}

	body, html {
		margin: 0;
		padding: 0;
		width: 100%;
		height: 100%;
	}

	body {
		font-size: 15px;
		color: black;
	}

	.menuresto {
		background-color: black;
		color: white;
		font-weight: 600;
		font-size: 20px;
		width: 100%;
		display: flex;
		padding: 10px;
		justify-content: space-between;
	}

	.navresto {
		background-color: red;
		font-weight: 500;
		font-size: 14px;
		width: 100%;
		display: flex;
	}

	.navresto a {
		display: inline-block;
		padding: 21px;
		text-decoration: none;
		color: black;
		min-width: 44px;
		min-height: 44px; 
	}
	
	.menu-tombol a {
		text-decoration: none;
		color: white;
		min-width: 44px; 
		min-height: 44px; 
	}
	
	.nav {
		background-color: black;
		width: 100%;
		display: flex;
		font-weight: 500;
		font-size: 14px;
		text-transform: uppercase;
	}

	.nav-list {
		width: 70%;
		padding: 0;
		margin: 0;
		text-align: right;
	}

	.nav-item {
		box-sizing: border-box;
		display: inline-block;
		text-align: right;
		line-height: 20px;
		text-transform: uppercase;
		position: relative;
		transition: color 0.3s;
	}

	.nav-item a {
		position: relative;
		z-index: 1;
		overflow: hidden;
	}

	.nav-item:hover {
		color: transparent;
	}

	.nav-item:hover a::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: linear-gradient(to bottom, transparent 0%, white 100%);
		transform: translateY(100%);
		transition: transform 0.3s ease-in-out;
	}

	.nav-item:hover a::before {
		transform: translateY(0);
	}

	.nav a {
		display: inline-block;
		padding: 15px;
		text-decoration: none;
		color: white;
	}

	.nav a:hover {
		text-decoration: none;
	}

	.logo {
		width: 30%;
		text-align: left;
		box-sizing: border-box;
		display: inline-block;
		line-height: 24px;
	}
	.hero {
		background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
		display: grid;
		background-size: cover;
		background-size: cover;
		background-position: center;
		place-content: center;
		text-align: center;
		min-height: 550px;
		width: 100%;
	}
	.hero-tittle {
		margin: 0 auto;
		max-width: 800px;
	}

	.header-title {
		font-weight: 500;
		font-size: 70px;
		color: #fff;
		background-color: rgba(0, 0, 0, 0.397);
	}

	.header-sub {
		margin-top: 6px;
		font-size: 48px;
		color: white;
		text-shadow: 1px 1px black;
	}

	main {
		width: 100%;
		margin: 0 auto;
	}

	.content {
		padding: 32px;
		margin-top: 10px;
		box-shadow: 0 2px 4px black;
		border-radius: 8px;
	}

	.latest {
		width: 100%;
		margin-top: 30px;
		margin-bottom: 50px;
		display: flex;
		flex-direction: column;
		text-align: center;
		justify-content: center;
		align-items: center;
	}

	.latest h1 {
		font-weight: 700;
		font-size: 32px;
		margin-bottom: 50px;
	}

	.latest h2 {
		position: relative;
		font-size: 2em;
		color: black;
		border-radius: 8px;
		margin-top: -40px;
	}

	.list {
		margin: 32px auto auto;
		text-align: left;
		display: grid;
		grid-row-gap: 16px;
	}

	.list_item {
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
		width: 100%;
		border-radius: 5px;
		overflow: hidden;
		position: relative;
	}

	.city {
		position: absolute;
		font-weight: bold;
		top: 10px;
		left: 20px;
		border-radius: 3px;
		padding: 5px 10px;
		background-color: rgb(238, 8, 8);
		color: white;
		
	}

	.list_item_content {
		padding: 16px;
	}

	.list_item_thumb {
		width: 100%;
		max-height: 200px;
	}

	.list_item_rating {
		position: absolute; 
		top: 39px;
		left: 20px;
		border-radius: 3px;
		padding: 5px 10px;
		font-size: 12px;
		font-weight: 500;
		color: white;
		background-color: rgba(0, 0, 0, 0.635);
		
	}

	.list_item_rating_value {
		color: gold;
		text-decoration: none;
	}

	.list_item_title {
		font-weight: 500;
		font-size: 16px;
		margin-top: 10px;
		transition: 0.3s opacity;
	}

	.list_item_title:hover {
		opacity: 0.5;
	}

	.list_item_title a {
		text-decoration: none;
		color: black;
	}

	.list_item_desc {
		margin-top: -30px;
		font-size: 12px;
		line-height: 1.5em;
	}

	footer {
		background-color: black;
		color: white;
		font-size: 14px;
		padding: 2em;
		margin-top: 10px;
		width: 100%;
		text-align: center;
	}

	.skip-link {
		position: absolute;
		top: -100px;
		background-color: red;
		color: white;
		padding: 10px;
		font-size: 20px;
		z-index: 100;
		text-decoration: none;
	}
	.skip-link::after {
		content: "\\25BC"; 
		display: inline-block;
		margin-left: 5px;
		font-size: 20px;	
	  }
	.skip-link:focus {
		top: 150px;
	}

	@media screen and (max-width: 1199px) {
		.header {
		background-size: 100%;
		}
	}
	@media screen and (min-width: 500px) {
		.menuresto {
			display: none;
		}
		.navresto {
			display: none;
		}
	}

	@media screen and (max-width: 499px) {
		.nav {
			display: none;
		}
		.navresto {
			z-index: 10;
			background-color: #fff;
			width: 100%;
			position: absolute;
			-webkit-transform: translate(-100%, 0);
			transform: translate(-100%, 0);
			transition: transform 0.3s ease;
		}
		.open {
			-webkit-transform: translate(0, 0);
			transform: translate(0, 0);
		}
	}

	@media screen and (min-width: 650px) {
		.list_item_content {
			padding: 16px 32px 32px 32px;
		}
		.list_item_title {
			font-size: 18px;
		}
		.list_item_desc {
			font-size: 14px;
		}
	}

	@media screen and (min-width: 700px) {
		.list {
			display: grid;
			grid-template-columns: 1fr 1fr;
			grid-column-gap: 10px;
			grid-row-gap: 16px;
		}	
	}

	@media screen and (min-width: 900px) {
		.list {
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
		}
	}

	@media screen and (min-width: 1200px) {
		main {
			max-width: 1200px;
		}
	}

	.header {
		transition: min-height 0.3s;
	  }
	  
	  .header__inner {
		width: 100%;
		text-align: center;
		margin-left: auto;
		margin-right: auto;
	  }
	  
	  .header__title {
		font-weight: 300;
		font-size: 4em;
		margin: 0.5em 0.25em;
		display: inline-block;
		color: #212121;
	  }
	  
	  .header__menu {
		font-size: 32px;
		display: block;
		width: 30px;
		padding: 44px;
	  }
	  
	  .header h1{
		font-size: 32px;
		margin: 10px auto;
		display: block;
		width: 30px;
	  }
	  
